import {
  Box,
  Button,
  FormControl,
  FormLabel,
  Image,
  Input,
  useBreakpointValue,
  VStack,
} from '@chakra-ui/react';
import { useState } from 'react';

import { useJoin } from '@/api/hooks/useJoin';
import KAKAO_LOGO from '@/assets/kakao_logo.svg';

export const JoinPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const { mutate: join } = useJoin();

  const handleJoin = () => {
    if (!email || !password) {
      alert('  .');
      return;
    }

    join({ email, password });
  };

  const formSpacing = useBreakpointValue({ base: 5, sm: 8 });

  return (
    <Box
      display="flex"
      flexDirection="column"
      justifyContent="center"
      alignItems="center"
      width="100vw"
      height="100vh"
    >
      <Image src={KAKAO_LOGO} alt=" CI" boxSize="88px" />
      <VStack
        as="form"
        spacing={formSpacing}
        w="full"
        maxW="580px"
        p={{ base: 4, sm: 10 }}
        border={{ sm: '1px' }}
        borderColor={{ sm: 'gray.200' }}
      >
        <FormControl isRequired>
          <FormLabel htmlFor="email"></FormLabel>
          <Input
            id="email"
            type="email"
            placeholder=" "
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </FormControl>

        <FormControl isRequired>
          <FormLabel htmlFor="password"></FormLabel>
          <Input
            id="password"
            type="password"
            placeholder=" "
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </FormControl>

        <Button onClick={handleJoin}></Button>
      </VStack>
    </Box>
  );
};
